import React from 'react'
import styled from 'styled-components'

function Section({title, description,rightBtnText, leftBtnText, backgroundImg}) {
  return (
    <Wrap bgImage={backgroundImg}>
        <ItemText>
            <h1>{ title }</h1>
            <p>{ description }</p>
        </ItemText>

        <Buttons>
            <ButtonGroup>
                <LeftButton>
                   {leftBtnText}
                </LeftButton>
                { rightBtnText &&
                <RightButton>
                   {rightBtnText}
                </RightButton>
                }
            </ButtonGroup>
            <DownArrow src='./images/down-arrow.svg'/>
        </Buttons>
    </Wrap>
  )
}

export default Section

const Wrap =  styled.div`
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('./images/model-3.jpg');
    // background and button to cover
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    // pass the bg images as props form Wrap
    background-image: ${props => `url("/images/${props.bgImage}")`};
`

const ItemText = styled.div`
    padding-top: 10vh;
    text-align: center;
`

const ButtonGroup = styled.div`
    display: flex;
    margin-bottom: 30px;
    // use media queries to apply different styles for different media types/devices.
    @media (max-width: 768px) {
        flex-direction: column;
    }
`
const LeftButton = styled.div`
    background-color:  rgba(23, 26, 32, 0.8);
    height: 40px;
    width: 256px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    opacity: 0.85;
    text-transform: uppercase;
    font-size: 12px;
    cursor: pointer;
    margin: 8px;
    // margin between two buttons and arrow
`
// style same as left button
const RightButton = styled(LeftButton)`
    background: white;
    opacity: 0.65;
    color: black;
`

const DownArrow = styled.img`
    height: 40px;
    animation: animateDown infinite 1.5s;
    overflow-x : hidden;
`
const Buttons = styled.div`

`
